//* Refactor tabs.js to accomodate multiple tab-groups

class Tabs {
    constructor(args) {
        this.tabTriggers = document.querySelectorAll('[data-tab]');
        this.panels = document.querySelectorAll('[data-tab-panel]');
        this.setInitialActive(this.tabTriggers)
        this.addEvents(this.tabTriggers)
    }

    addEvents(tabTriggers) {
        tabTriggers.forEach(tab => {
            tab.addEventListener('click', () => {
                var tabTrigger = tab.dataset.tab
                var panel = document.querySelector(`[data-tab-panel="${tabTrigger}"]`)
                this.resetTabs(tabTriggers, this.panels)
                this.switchTab(tab, panel)
            })
        })
    }

    switchTab(tab, panel) {
        tab.classList.toggle("is-active");
        panel.classList.toggle("is-active");
    }

    resetTabs(tabTriggers, panels) {
        for (let i = 0; i < panels.length; i++) {
            panels[i].classList.remove('is-active')
        }
        for (let i = 0; i < tabTriggers.length; i++) {
            tabTriggers[i].classList.remove('is-active')
        }
    }

    resetSiblingTabs(tabTrigger) {
        let siblingTabs = tabTrigger.siblings(".tabs")
        for (let i = 0; i < siblingTabs.length; i++) {
            siblingTabs[i].classList.remove('is-active')
        }
    }

    setInitialActive(tabTriggers) {
        for (let i = 0; i < tabTriggers.length; i++) {
            let tab = tabTriggers[i]
            let panel = document.querySelector(`[data-tab-panel="${tab.dataset.tab}"]`)

            if (tab.classList.contains('is-active')) {
                tab.classList.add("is-active");
                panel.classList.add("is-active");
                console.log("Tabs :: Active Panel set")
            }
        }
    }
}

const tabs = new Tabs()