export class Modal {
    constructor(args) {
        this.modalTriggers = document.querySelectorAll('[data-modal]')
        this.modalCloseTriggers = document.querySelectorAll('[data-modal-close]')
        this.sidenav = document.querySelector('.sidenav')
        this.modals = document.querySelectorAll('.modal')
        this.body = document.querySelector("body");

        this.isOpen = false;

        this.addEventHandlers(this.modalTriggers, this.body)
    }

    open(modal, body) {
        if (this.isOpen === true) {
            return;
        }
        modal.classList.add("is-open");
        body.classList.add("has-overlay");

        this.isOpen = true;
    }
    
    close(modal, body) {
        if (this.isOpen === false) {
            return;
        }

        modal.classList.remove("is-open");
        body.classList.remove("has-overlay");

        this.isOpen = false;

    }

    addEventHandlers(modalTriggers, body) {
        modalTriggers.forEach(trigger => {
            trigger.addEventListener('click', () => {
                const modalTrigger = trigger.dataset.modal
                const modal = document.querySelector(`[id="${modalTrigger}"]`)
        
                if (modal.classList.contains('is-open') && body.classList.contains('has-overlay')) {
                    this.close(modal, this.body)
                } else {
                    this.open(modal, this.body)
                }
        
                modal.querySelector('[data-modal-close]').addEventListener('click', () => {
                    this.close(modal, this.body)
                })
            })
        })
    }

    removeClasses(body, modals) {
        console.log(modals)
        for (var i = 0; i < modals.length; i++) {
            modals[i].classList.remove('is-open')
        }
        body.classList.remove('has-overlay')
    }
}

// export instance of MyModule directly
export default new Modal();